import Vue from 'vue'
import Router from 'vue-router'


//base views
import Home from './views/Base/Home/Home'
import Blog from './views/Base/Blog'
import Article from './views/Base/Article'
import Demo from './views/Base/Demo'

//admin views
import Editor from './views/Admin/Editor'


import NotFound from './views/NotFound'


//demos
import DemoCalculator from './views/Base/Demo/childrens/Calculator'
import DemoThrottle from './views/Base/Demo/childrens/Throttle'
import DemoDebounce from './views/Base/Demo/childrens/Debounce'



Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [

    {
      path: '',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home,
      meta: {
        hideHeader: true
      }
    },
    {
      path: '/editor',
      component: Editor,
      meta: {
        admin: true,
        title: ""
      }
    },
    {
      path: '/blog',
      component: Blog,
      meta: {
        title: "",
        keepalive: true
      }
    },
    {
      path: '/article/:id',
      component: Article,
      meta: {
        title: ""
      }
    },
    {
      path: '/demo',
      name: 'Demo',
      component: Demo,
      meta: {
        title: ""
      },
      children: [
        {
          path: "calculator",
          name: "Calculator",
          component: DemoCalculator,
          meta: {
            title: "Mac"
          },
        },
        {
          path: "throttle",
          name: "Throttle",
          component: DemoThrottle,
          meta: {
            title: ""
          },
        },
        {
          path: "debounce",
          name: "Debounce",
          component: DemoDebounce,
          meta: {
            title: ""
          },
        },
        {
          path: '*',
          component: NotFound,
          meta: {
            title: "404"
          }
        }
      ]
    },
    {
      path: '*',
      component: NotFound,
      meta: {
        hideHeader: true,
        title: "404"
      }
    }
  ]
})